(function() {
    'use strict';
    
    angular
        .module('oncoscape')
        .directive('osDatasource', datasource);

    /** @ngInject */
    function datasource() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/datasource/datasource.html',
            controller: DatasourceController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function DatasourceController(osApi, $state) {
            var vm = this;
            vm.datasets = [];
            vm.explore = function(tool, datasource) {
                osApi.setDataSource(datasource);
                $state.go(tool, {
                    datasource: datasource
                });
            };

            var nameMap = {
                "DEMOdz" : "DEMO (TCGA)",
                "TCGAbrain": "Glioma (TCGA)",
                "TCGAbrca": "Breast (TCGA)",
                "TCGAcoadread": "Colorectal (TCGA)",
                "TCGAgbm": "Glioblastoma (TCGA)",
                "TCGAhnsc": "Head + Neck (TCGA)",
                "TCGAlgg" : "Lower Grade Glioma (TCGA)",
                "TCGAluad": "Lung Adenocarcinoma (TCGA)",
                "TCGAlung": "Lung (TCGA)",
                "TCGAlusc": "Lung Squamous Cell (TCGA)",
                "TCGApaad": "Pancreas (TCGA)",
                "TCGAprad": "Prostate (TCGA)",
                "UWbrain":  "Brain (UW)",
                "MSKbrain": "Brain (MSK)"
            };

            // Load Datasets
            vm.datasets = osApi.getUserApi().getUser().datasets.map(function(item){
                    return {name: this[item], id: item}
                }, nameMap);
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osDataExplorer', explore);

    /** @ngInject */
    function explore() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/dataexplorer/dataexplorer.html',
            controller: DataExplorerController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function DataExplorerController(osApi, $state, $timeout, $scope, $stateParams, _) {

            if (angular.isUndefined($stateParams.datasource)){
                $state.go("datasource");
                return;
            }
            // View Model
            var vm = this;
            vm.dataSources;
            vm.dataSource;
            vm.dataDiseases;
            vm.dataDisease;
            vm.dataTables;
            vm.dataTable;
            vm.dataFields;
            vm.dataField;
            
            var bar = {
                chart: {
                    type: 'discreteBarChart',
                    height: 450,
                    margin : {
                        top: 20,
                        right: 20,
                        bottom: 50,
                        left: 55
                    },
                    x: function(d){return d.label;},
                    y: function(d){return d.value;},
                    showValues: true,
                    valueFormat: function(d){
                        return d3.format(',.4f')(d);
                    },
                    duration: 500,
                    xAxis: {
                        axisLabel: 'X Axis'
                    },
                    yAxis: {
                        axisLabel: 'Y Axis',
                        axisLabelDistance: -10
                    }
                }
            };

            var pie = {
                chart: {
                    type: 'pieChart',
                    height: 450,
                    donut: false,
                    x: function(d){return d.label; },
                    y: function(d){return d.value; },
                    showLabels: false,
                    pie: {
                        startAngle: function(d) { return d.startAngle/2 -Math.PI/2 },
                        endAngle: function(d) { return d.endAngle/2 -Math.PI/2 }
                    },
                    duration: 500
                }
            };

            vm.chartOptions = bar;

            vm.toolTipContentFunction = function(){
                return function(key, x, y, e, graph) {
                        return  'Super New Tooltip' +
                        '<h1>' + key + '</h1>' +
                        '<p>' +  y + ' at ' + x + '</p>'
                }
            }

                

            vm.xFunction = function(){
                return function(d) {
                    return d.label;
                };
            }
            
            vm.yFunction = function(){
                return function(d) {
                    return d.value;
                };
            }
          
        

            // Load Datasets
            osApi.setBusy(true);
            osApi.query("_collections").then(function(result){

                vm.dataSources = result.data;
                vm.dataSource = vm.dataSources[0];
                
                $scope.$watch("vm.dataSource", function(){
                    vm.dataDiseases = vm.dataSource.diseases;
                    vm.dataDisease = vm.dataSource.diseases[0];

                });
                $scope.$watch("vm.dataDisease", function(){
                    vm.dataTables = vm.dataDisease.tables;
                    vm.dataTable = vm.dataTables[0];
                });
                $scope.$watch("vm.dataTable", function(){
                    osApi.query("_field_detail", {collection:vm.dataTable.collection}).then(function(result){
                         vm.dataFields = result.data[0].fields.filter(function(field){ return field.key!="_id"; });
                         vm.dataField = vm.dataFields[0];
                         console.dir(vm.dataField)
                    });
                });

               osApi.setBusy(false);
               
            });
            
        }
    }
})();


(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osCohorts', cohorts);

    /** @ngInject */
    function cohorts() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/cohorts/cohorts.html',
            controller: CohortsController,
            scope:{
                type:"@"
            },
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function CohortsController(osHistory, $state, $timeout) {

            var vm = this;
            vm.selection;
            vm.selections;
            
            vm.close = function(){
                angular.element(".cohorts-"+vm.type).hide();
            }
            vm.setSelection = function(item){
                osHistory["set"+vm.type+"Selection"](item);
            };
            var update = function(){
                $timeout(function() {
                    vm.selections = osHistory["get"+vm.type+"Selections"]();
                    vm.selection = osHistory["get"+vm.type+"Selection"]();
                    
                });
            };
            
            osHistory["on"+vm.type+"Add"].add(function(){update(true)});            
            osHistory["on"+vm.type+"SelectionChange"].add(function(){update(false)});
            
            var isDragable = false;
            var show = function(){
                angular.element(".cohorts-"+vm.type).show();
                if (isDragable) return;
                isDragable = true;
                angular.element(".cohorts-"+vm.type).draggable();

            }            

            // Keypress
            angular.element(document).keypress(function(e) {
                if (!e.ctrlKey) return;
                if(vm.type=="Gene"){
                    switch(e.keyCode){
                        case 12: show(); break;
                        case 39: osHistory.getGeneSelectionNext(); break;
                        case 59: osHistory.getGeneSelectionLast(); break;
                    }
                }else{
                    switch (e.keyCode)
                    {
                        case 16: show(); break;
                        case 27: osHistory.getPatientSelectionLast(); break;
                        case 29: osHistory.getPatientSelectionNext(); break;                
                    }
                }
                
            });
        }
    }
})();

(function() {
    'use strict';

    angular
        .module('oncoscape')
        .directive('osCohortBuilder', cohortBuilder);

    /** @ngInject */
    function cohortBuilder() {

        var directive = {
            restrict: 'E',
            templateUrl: 'app/components/cohortbuilder/cohortbuilder.html',
            controller: CohortBuilderController,
            controllerAs: 'vm',
            bindToController: true
        };

        return directive;

        /** @ngInject */
        function CohortBuilderController(osApi, $state, $timeout, $scope, $stateParams, _) {

            if (angular.isUndefined($stateParams.datasource)){
                $state.go("datasource");
                return;
            }
            // View Model
            var vm = this;
            vm.dataSources;
            vm.dataSource;
            vm.dataDiseases;
            vm.dataDisease;
            vm.dataTables;
            vm.dataTable;
            vm.dataFields;

            vm.toolTipContentFunction = function(){
                return function(key, x, y, e, graph) {
                        return  'Super New Tooltip' +
                        '<h1>' + key + '</h1>' +
                        '<p>' +  y + ' at ' + x + '</p>'
                }
            }

                

            vm.xFunction = function(){
                return function(d) {
                    return d.label;
                };
            }
            
            vm.yFunction = function(){
                return function(d) {
                    return d.value;
                };
            }
          

            // Load Datasets
            osApi.setBusy(true);
            osApi.query("_collections").then(function(result){
                
                // Transform DataSources
                var data = result.data;
                vm.dataSources = 
                    _.chain(data).pluck("source").uniq().map(function(v){return { name:v,
                        diseases:_.chain(data).where({source:v}).pluck("disease").uniq().map(function(v){ 
                            return { name:v,
                                tables: _.chain(data).where({source:"tcga",disease:v}).map(function(v){ 
                                    return {created:v.created, name:v.table, records:v.records, collection:v.collection}} ).value()
                            };}).value()
                    };}).value();
                vm.dataSource = vm.dataSources[0];
                
                $scope.$watch("vm.dataSource", function(){
                    vm.dataDiseases = vm.dataSource.diseases;
                    vm.dataDisease = vm.dataSource.diseases[0];

                });
                $scope.$watch("vm.dataDisease", function(){
                    vm.dataTables = vm.dataDisease.tables;
                    vm.dataTable = vm.dataTables[0];
                    
                });
                $scope.$watch("vm.dataTable", function(){
                    console.log(osApi.queryString("_stats", {collection:vm.dataTable.collection}));
                    osApi.query("_stats", {collection:vm.dataTable.collection}).then(function(result){
                        vm.dataFields = result.data[0].fields;
                    });
                });

               osApi.setBusy(false);
               
            });
            
        }
    }
})();


